// @flow
import _get from 'lodash/get';
import type { Offer as OfferType } from 'types/Offer';
import { formatPrice } from 'helpers/number';
import { getEarnestMoneyDeposit } from 'helpers';
import _isEmpty from 'lodash/isEmpty';
import {
  displayLoanType,
  displayContingency,
  displayFurniture,
  getBuyerName,
  getFullName,
} from '../../../helpers/Offer';

type fieldType = {
  label: string,
  value: string | number,
};

export const getOriginalOfferDialogFields = (
  offer: OfferType,
): Array<fieldType> => {
  if (!offer || _isEmpty(offer)) return [];

  const offerPrice = +_get(offer, 'Offer.OfferPrice');
  const earnestMoneyDeposit = getEarnestMoneyDeposit(offerPrice);

  return [
    { label: 'Offer No', value: _get(offer, 'OfferNo', '') },
    {
      label: 'offer price',
      value: offerPrice ? formatPrice(_get(offer, 'Offer.OfferPrice')) : 'TBD',
    },
    {
      label: 'Earnest Money Deposit',
      value: earnestMoneyDeposit,
    },
    {
      label: 'funding',
      value: _get(offer, 'Funding.PurchaseType', ''),
    },
    {
      label: 'Loan Type',
      value: displayLoanType(offer),
    },
    {
      label: 'finance contingency',
      value: displayContingency(offer, 'Offer.FinanceContingency'),
    },
    {
      label: 'inspection contingency',
      value: displayContingency(offer, 'Offer.InspectionContingency'),
    },
    {
      label: 'appraisal contingency',
      value: displayContingency(offer, 'Offer.AppraisalContingency'),
    },
    {
      label: 'home sale contingency',
      value: displayContingency(offer, 'Offer.SaleOfHomeContingency'),
    },
    {
      label: 'Closing Period',
      value: _get(offer, 'Offer.ClosingPeriod', ''),
    },
    {
      label: 'Furniture',
      value: displayFurniture(
        _get(offer, 'Offer.Furniture', ''),
        _get(offer, 'Offer.FurnitureOther', ''),
      ),
    },
    { label: 'Buyer Name', value: getBuyerName(offer) },
    {
      label: 'Buyer Agent Name',
      value: getFullName(_get(offer, 'AgentInfo', {})),
    },
    {
      label: 'Nile Listing Agent Name',
      value: 'Khaled Yatim',
    },
    {
      label: 'Property Address',
      value: _get(offer, 'Property.PropertyData.FullAddress', ''),
    },
  ];
};
